body {
  background: linear-gradient(to bottom, #33ccff 0%, #ff99cc 100%);
  background-attachment: fixed;
}

.App,
.text {
  font-family: "Times New Roman", Times, serif;
  font-size: 40px;
  text-align: center;
}

.button {
  font-size: 20px;
  padding: 30px;
  place-items: center;
  background-color: orange;
  border-radius: 10px;
}

.container-btnInicio {
  margin: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.btnInicio {
  font-size: 20px;
  padding: 30px;
  background-color: silver;
  border-radius: 10px;
}

.btnInicio:hover {
  background-color: green;
  transition-delay: 0.1s;
}

.btnMenor {
  font-size: 20px;
  padding: 30px;
  background-color: yellowgreen;
  border-radius: 10px;
}

.btnMaior {
  font-size: 20px;
  padding: 30px;
  background-color: burlywood;
  border-radius: 10px;
}

.btnAcertou {
  font-size: 20px;
  margin: 20px;
  padding: 30px;
  background-color: red;
  border-radius: 10px;
}
.result {
  text-align: center;
}
